<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li class="menu-item current"
            v-for="(item,index) in goods"
            @click="menuCick(index, $event)"
            :class="index==menuCurrentIndex?'menu-item-select':'menu-item'">
          <span class="text">
            <icon-map v-if="item.type>0" class="iconMap" :icon-type="item.type" ></icon-map>
            {{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodWrapper">
      <ul>
        <li v-for="item in goods" class="food-list food-list-hook">
          <h1>{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" class="food-item" @click="showFoodDetail(food,$event)">
              <div class="icon">
                <img width="57" height="57" :src="food.icon">
              </div>
              <div class="content">
                <h2>{{food.name}}</h2>
                <p class="description" v-show="food.description">{{food.description}}</p>
                <div class="sell-info">
                  <span class="sellCount">{{food.sellCount}}</span>
                  <span class="sellRating">{{food.sellCount}}%</span>
                </div>
                <div class="price">
                  <span class="newPrice">
                    <span class="unit"></span>
                    {{food.price}}
                  </span>
                  <span v-show="food.oldPrice" class="oldPrice">{{food.oldPrice}}</span>
                </div>
                <div class="carControl-wrapper">
                  <cart-control :food="food"></cart-control>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shop-cart :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice" :selectFoods="selectFoods">
    </shop-cart>
    <!--v-showv-ifv-show-->
    <food-detail :food="selectedFood" v-if="selectedFood" ref="myFood"></food-detail>
  </div>
</template>
<script>
  import axios from 'axios'
  import BScroll from 'better-scroll'
  import iconMap from './iconMap'
  import cartControl from './cartControl'
  import shopCart from './shopCart'
  import foodDetail from './foodDetail'

  export default {
    name: 'goods',
    data() {
      return {
        listHeight: [],
        selectedFood: '',
        foodsScrollY: 0, // 
      }
    },
    props:{
      seller: {
        type: Object
      },
      goods: Array
    },
    computed: {
      menuCurrentIndex () { // 
        for (let i=0; i < this.listHeight.length; i++) {
          let topHeight = this.listHeight[i]
          let bottomHeight = this.listHeight[i+1]
          //  <= <
          if (!bottomHeight || (this.foodsScrollY >= topHeight && this.foodsScrollY < bottomHeight)) {
            return i
          }
        }
        return 0
      },
      selectFoods () {
        let foods = []
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food)
            }
          })
        })
        return foods
      }
    },
    components: {
      iconMap,
      cartControl,
      shopCart,
      foodDetail
    },
    methods: {
      _initDo() {
        this.$nextTick(() => {
          this._initScroll()
          this._calculateHeight()
        })
      },
      _initScroll () {
        this.menuscroll = new BScroll(this.$refs.menuWrapper, {click: true})
        this.foodscroll = new BScroll(this.$refs.foodWrapper, {click: true, probeType: 3})
        //  probeType  3  momentum  scroll 
        //  0 scroll 
        // console.log(this.foodscroll)

        // 
        this.foodscroll.on('scroll', (pos) => {
          this.foodsScrollY = Math.abs(Math.round(pos.y))
          // console.log(this.foodsScrollY)
        })
      },
      // 
      _calculateHeight () {
        let foodList = this.$refs.foodWrapper.querySelectorAll('.food-list-hook')
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i]
          height += item.clientHeight
          this.listHeight.push(height)
        }
        // console.log(this.listHeight)
      },
      // 
      menuCick (index, e) {
        // ,
        if (!e._constructed){
          return
        }
        // 
        this.foodscroll.scrollTo(0, -this.listHeight[index], 300)
      },
      showFoodDetail (food, e) {
        if (!e._constructed) {
          return
        }
        // console.log(food)
        this.selectedFood = food
        this.$nextTick(() => {
          // console.log(this.$refs.myFood)
          // 
          // v-ifv-show 
          // this.refsv-iffalse
          this.$refs.myFood.showToggle()
        })
      }
    },
      created() {
        this._initDo()
      }
    }

</script>
<style scoped lang="less">
@import "./goods.less";
</style>



